import React from "react";

import { Grid, Typography, List, ListItem } from "@material-ui/core";

function News() {
  return (
    <div>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <List>
            <ListItem>
              <Typography variant="h6">
                [2021/05/17] Welcome to my homepage!
              </Typography>
            </ListItem>
          </List>
        </Grid>
      </Grid>
    </div>
  );
}

export default News;
