import React from "react";

import { Grid, Typography, List, ListItem } from "@material-ui/core";

function Activities() {
  return (
    <div>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <List>
            <Typography variant="h5">Reviewer for</Typography>
            <ListItem>
              <Typography variant="h6">
                IEEE Transactions on Mobile Computing
              </Typography>
            </ListItem>
            <ListItem>
              <Typography variant="h6">
                IEEE Transactions on Cognitive Communications and Networking
              </Typography>
            </ListItem>
            <ListItem>
              <Typography variant="h6">
                IEEE Transactions on Human-Machine Systems
              </Typography>
            </ListItem>
            <ListItem>
              <Typography variant="h6">
                IEEE Transactions on Network Science and Engineering
              </Typography>
            </ListItem>
            <ListItem>
              <Typography variant="h6">
                IEEE Transactions on Industrial Informatics
              </Typography>
            </ListItem>
            <ListItem>
              <Typography variant="h6">Elsevier Computer Networks</Typography>
            </ListItem>
            <ListItem>
              <Typography variant="h6">Springer Wireless Networks</Typography>
            </ListItem>
          </List>
        </Grid>
      </Grid>
    </div>
  );
}

export default Activities;
