import React from "react";

import { Grid, Typography, Divider, List, ListItem } from "@material-ui/core";

import Photo from "../photo.jpg";

function Home() {
  return (
    <div>
      <Grid container spacing={3}>
        <Grid item xs={1.5}>
          <img alt="Xi Tao" src={Photo} />
        </Grid>
        <Grid item xs={3}>
          <Typography variant="h6">
            Postdoctoral Researcher
            <br />
            <br />
            University of Montreal
            <br />
            Department of Computer Science and Operations Research
            <br />
            Email: &nbsp;
            <a class="link" href="mailto:xi.tao@umontreal.ca">
              xi.tao@umontreal.ca
            </a>
            <br />
          </Typography>
        </Grid>
        <Grid item xs={7}>
          <Typography variant="h6">
            I am currently a Postdoctoral Researcher with the Department of
            Computer Science and Operations Research, University of Montreal,
            Montreal, QC, Canada. My research interests include mobile
            crowdsensing, edge computing, and IoT. I received the B.Eng. and
            M.Eng. degrees in electrical engineering from Xi’an Jiaotong
            University, Xi’an, China, in 2013 and 2016, respectively, and the
            Ph.D. degree in computer science from the University of New
            Brunswick, Fredericton, NB, Canada, in 2020.
          </Typography>
        </Grid>
      </Grid>
      <Divider />
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <List>
            <Typography variant="h5">Work Experience</Typography>
            <ListItem>
              <Typography variant="h6">
                Postdoctoral Researcher, University of Montreal, Montreal, QC,
                Canada (2020.02 - ) &nbsp;
                <a class="link" href="http://www.iro.umontreal.ca/~ahafid/">
                  Supervisor: Dr. Abdelhakim Senhaji Hafid
                </a>
              </Typography>
            </ListItem>
          </List>
        </Grid>
      </Grid>
      <Divider />
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <List>
            <Typography variant="h5">Education</Typography>
            <ListItem>
              <Typography variant="h6">
                Ph.D. in Computer Science, University of New Brunswick,
                Fredericton, NB, Canada (2016.09 to 2020.01) &nbsp;{" "}
                <a class="link" href="http://www.cs.unb.ca/~wsong/">
                  Supervisor: Dr. Wei Song
                </a>
              </Typography>
            </ListItem>
            <ListItem>
              <Typography variant="h6">
                Master of Electrical Engineering, Xi’an Jiaotong University,
                Xi’an, Shaanxi, China (2013.09 to 2016.06) &nbsp;{" "}
                <a class="link" href="http://ee.xjtu.edu.cn/info/1181/5068.htm">
                  Supervisor: Dr. Shuhong Wang
                </a>
              </Typography>
            </ListItem>
            <ListItem>
              <Typography variant="h6">
                Bachelor of Electrical Engineering, Xi’an Jiaotong University,
                Xi’an, Shaanxi, China (2009.09 to 2013.06)
              </Typography>
            </ListItem>
          </List>
        </Grid>
      </Grid>
    </div>
  );
}

export default Home;
