import React from "react";

import {
  Grid,
  AppBar,
  Toolbar,
  Typography,
  Tabs,
  Tab,
} from "@material-ui/core";

function NavBar() {
  return (
    <div>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <AppBar position="static">
            <Toolbar>
              <Typography variant="h4">
                Welcome to Xi Tao's Homepage!
              </Typography>
            </Toolbar>
            <Toolbar>
              <Tabs>
                <Tab label="Home" href="/home" />
                <Tab label="Publications" href="/publications" />
                <Tab label="Professional Activities" href="/activities" />
                <Tab label="News" href="/news" />
              </Tabs>
            </Toolbar>
          </AppBar>
        </Grid>
      </Grid>
    </div>
  );
}

export default NavBar;
